var unique = function(arr) {
    var result = [], hash = {};
    var item;
    for (var i = 0; (item = arr[i]) != null; i++) {
        var temp = item.replace(/[ol](?=s|b)/,'x').replace(/&[ol]/,''); //
        if (!hash[item]&&!hash[temp]){
            result.push(item);
            hash[item] = true;
        }
    }
    return result;
};

function getSelectors(str){
    str = str.replace(/[||||||||]{1,2}/g,'x');
    var lists = str.split('');
    var result = [];                        //
    var match = true;
    while(lists.length){
        var name = lists.shift();           //
        var arr = [];                       //
        var has = false;
        for(var i in _data){
            var value = _data[i];
            if(value.indexOf(name)>-1){     //
                if(i||!lists.length){       //
                    arr.push(i);
                }
                has = true;
            }
        }
        if(!has){
            match = false;
        }
        if(result.length){                  //
            var res = [];
            for(var i=0;i<result.length;i++){
                for(var j=0;j<arr.length;j++){
                    res.push(result[i] +','+arr[j]);
                }
            }
            result = res;
        }else{
            for(var i=0;i<arr.length;i++){
                result.push(','+arr[i]);
            }
        }
    }
    return match?result:[];
}

function selector2id(selector,sex){
    var result = [];
    var hash = {};
    var sex2 = -1;
    if(sex<0){
        if(selector.indexOf(',w')==0){
            sex = 1;
        }else if(selector.indexOf(',h')==0){
            sex = 0;
        }
    }
    if(sex>-1){
        selector = ','+sex+selector;
    }
    if(selector.match(/,[w0],w|,[h1],h/)){
        return false;
    }
    var getId = function(selector){
        var s='';
        if(!hash[selector]){
            hash[selector] = true;
            var status = true;
            do{
                s = selector;
                for(var i in _filter){
                    var item = _filter[i];
                    selector = selector.replace(item['exp'],item['str']);
                    if(selector.indexOf('#')>-1){
                        var arr = selector.split('#');
                        for(var j=0;j<arr.length;j++){
                            getId(arr[j]);
                        }
                        status=false;
                        break;
                    }
                }
            }while(s!=selector);
            if(status){
                if(selector.match(/,[w0],w|,[h1],h/)){
                    return false;
                }
                selector = selector.replace(/,[01]/,'').substr(1);
                result.push(selector);
            }
        }
    }
    getId(selector);
    return unique(result);
}

function getDataById(id){

    var items = [];
    var filter = /&[olx]/g;  //
    var getData = function(d){
        var res = [];
        for(var i in _data){
            if(i.replace(filter,'')==d){
                res.push(_data[i][0]);
            }
        }
        return res;
    };
    if(_data[id]){  //
        items.push(_data[id][0]);
    }else{
        items = getData(id);
        if(!items.length){  //
            id = id.replace(/&[ol]/g,'');
            items = getData(id);
        }
        if(!items.length){  //
            id = id.replace(/[ol]/g,'x');
            items = getData(id);
        }
        if(!items.length){  //
            var l = id.replace(/x/g,'l');
            items = getData(l);
            var o = id.replace(/x/g,'o');
            items = items.concat(getData(o));
        }
    }
    return items;
}

function getAllDataById(id){

    var items = [];
    var filter = /&[olx]/g;  //
    var getData = function(d){
        var res = [];
        for(var i in _data){
            if(i.replace(filter,'')==d){
                res.push(_data[i]);
            }
        }
        return res;
    };
    if(_data[id]){  //
        items.push(_data[id]);
    }else{
        items = getData(id);
        if(!items.length){  //
            id = id.replace(/&[ol]/g,'');
            items = getData(id);
        }
        if(!items.length){  //
            id = id.replace(/[ol]/g,'x');
            items = getData(id);
        }
        if(!items.length){  //
            var l = id.replace(/x/g,'l');
            items = getData(l);
            var o = id.replace(/x/g,'o');
            items = items.concat(getData(o));
        }
    }
    return items;
}

function reverseId(id,sex){
    var hash = {
        f:['d','s'],
        m:['d','s'],
        h:['w',''],
        w:['','h'],
        s:['m','f'],
        d:['m','f'],
        lb:['os','ob'],
        ob:['ls','lb'],
        xb:['xs','xb'],
        ls:['os','ob'],
        os:['ls','lb'],
        xs:['xs','xb']
    };
    var age = '';
    if(id.indexOf('&o')>-1){
        age = '&l';
    }else if(id.indexOf('&l')>-1){
        age = '&o';
    }
    if(id){
        id = id.replace(/&[ol]/g,'');
        sex = sex?1:0;      //
        var sid = (','+sex+','+id).replace(/,[fhs]|,[olx]b/g,',1').replace(/,[mwd]|,[olx]s/g,',0');
        sid = sid.substring(0,sid.lastIndexOf(','));
        var id_arr = id.split(',').reverse();
        var sid_arr = sid.split(',').reverse();
        var arr = [];
        for(var i=0;i<id_arr.length;i++){
            arr.push(hash[id_arr[i]][sid_arr[i]]);
        }
        return arr.join(',')+age;
    }
    return '';
}

function getChainById(id){
    var arr = id.split(',');
    var items = [];
    for(var i = 0;i<arr.length;i++){
        var key = arr[i].replace(/&[ol]/,'');
        items.push(_data[key][0]);
    }
    return items.join('');
}

function relationshipWorkout(parameter){
    var options = {
        text:'',
        sex:-1,
        type:'default',
        reverse:false
    };
    for (var p in parameter) {
        options[p] = parameter[p];
    }
    var selectors = getSelectors(options.text);
    var allResult = [];
    for(var i = 0;i<selectors.length;i++){
        var ids = selector2id(selectors[i],options.sex);
        for(var j=0;j<ids.length;j++){
            var id = ids[j];
            var items = getDataById(id);
            var allItems = getAllDataById(id);
            if(items.length){
                allResult = allResult.concat(allItems);
            }else if(id.indexOf('w')==0||id.indexOf('h')==0){
                items = getDataById(id.substr(2));
                allItems = getAllDataById(id.substr(2));
                if(items.length){
                    allResult = allResult.concat(allItems);
                }
            }
        }
    }
    var finalAllResult = [];
    for(var i = 0; i < allResult.length; i++)
    {
        var tempResult = unique(allResult[i]);
        finalAllResult.push(tempResult);
    }
    return finalAllResult;
}

//:0,:1 || str:'','','','','','','','','','',''
//
export function kinRelation(str,sx = 1)
{
    var query = {
        text:str,
        sex:sx,
        type:'default', 
        reverse:false
    }
    var result = relationshipWorkout(query);
    return result;
}




